'use client'

import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Loader2, Plus, Calendar, User, MapPin, DollarSign } from "lucide-react"
import { toast } from "sonner"
import { format } from 'date-fns'

interface Assignment {
  id: string
  resource_type: 'equipment' | 'vehicle'
  equipment_id?: string
  vehicle_id?: string
  crew_id: string
  project_id?: string
  assigned_at: string
  returned_at?: string
  daily_rental_cost?: number
  notes?: string
  created_at: string
  updated_at: string
}

interface AssignmentsResponse {
  items: Assignment[]
  page: number
  per_page: number
  total: number
  total_pages: number
}

export default function EquipmentAssignmentsPage() {
  const router = useRouter()
  const [assignments, setAssignments] = useState<Assignment[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const [pagination, setPagination] = useState({
    page: 1,
    per_page: 20,
    total: 0,
    total_pages: 0
  })

  useEffect(() => {
    loadAssignments()
  }, [])

  async function loadAssignments() {
    try {
      setIsLoading(true)
      const response = await fetch(`/api/equipment/assignments?page=${pagination.page}&per_page=${pagination.per_page}`)

      if (!response.ok) {
        throw new Error('Failed to load assignments')
      }

      const data: AssignmentsResponse = await response.json()
      setAssignments(data.items)
      setPagination({
        page: data.page,
        per_page: data.per_page,
        total: data.total,
        total_pages: data.total_pages
      })
    } catch (error) {
      console.error('Failed to load assignments:', error)
      toast.error('Failed to load assignments')
    } finally {
      setIsLoading(false)
    }
  }

  const getStatusBadge = (assignment: Assignment) => {
    if (assignment.returned_at) {
      return <Badge variant="secondary">Returned</Badge>
    }
    return <Badge variant="default">Active</Badge>
  }

  const getResourceTypeBadge = (type: string) => {
    return type === 'equipment' ? (
      <Badge variant="outline" className="bg-blue-50 text-blue-700 border-blue-200">
        Equipment
      </Badge>
    ) : (
      <Badge variant="outline" className="bg-green-50 text-green-700 border-green-200">
        Vehicle
      </Badge>
    )
  }

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <Loader2 className="h-8 w-8 animate-spin" />
      </div>
    )
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">Equipment Assignments</h1>
          <p className="text-muted-foreground">
            Manage equipment and vehicle assignments to crews
          </p>
        </div>
        <Button onClick={() => router.push('/dashboard/equipment/assignments/new')}>
          <Plus className="mr-2 h-4 w-4" />
          New Assignment
        </Button>
      </div>

      <div className="grid gap-4">
        {assignments.length === 0 ? (
          <Card>
            <CardContent className="flex flex-col items-center justify-center py-12">
              <div className="rounded-full bg-muted p-3 mb-4">
                <MapPin className="h-6 w-6 text-muted-foreground" />
              </div>
              <h3 className="text-lg font-semibold mb-2">No assignments found</h3>
              <p className="text-muted-foreground text-center mb-4">
                Get started by creating your first equipment assignment.
              </p>
              <Button onClick={() => router.push('/dashboard/equipment/assignments/new')}>
                <Plus className="mr-2 h-4 w-4" />
                Create Assignment
              </Button>
            </CardContent>
          </Card>
        ) : (
          assignments.map((assignment) => (
            <Card key={assignment.id} className="hover:shadow-md transition-shadow">
              <CardHeader className="pb-3">
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-3">
                    {getResourceTypeBadge(assignment.resource_type)}
                    {getStatusBadge(assignment)}
                  </div>
                  <div className="text-sm text-muted-foreground">
                    #{assignment.id.slice(-8)}
                  </div>
                </div>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-sm">
                  <div className="flex items-center gap-2">
                    <Calendar className="h-4 w-4 text-muted-foreground" />
                    <div>
                      <div className="font-medium">Assigned</div>
                      <div className="text-muted-foreground">
                        {format(new Date(assignment.assigned_at), 'MMM d, yyyy')}
                      </div>
                    </div>
                  </div>

                  <div className="flex items-center gap-2">
                    <User className="h-4 w-4 text-muted-foreground" />
                    <div>
                      <div className="font-medium">Crew ID</div>
                      <div className="text-muted-foreground">
                        {assignment.crew_id.slice(-8)}
                      </div>
                    </div>
                  </div>

                  {assignment.daily_rental_cost && (
                    <div className="flex items-center gap-2">
                      <DollarSign className="h-4 w-4 text-muted-foreground" />
                      <div>
                        <div className="font-medium">Daily Cost</div>
                        <div className="text-muted-foreground">
                          €{assignment.daily_rental_cost}
                        </div>
                      </div>
                    </div>
                  )}

                  {assignment.returned_at && (
                    <div className="flex items-center gap-2">
                      <Calendar className="h-4 w-4 text-muted-foreground" />
                      <div>
                        <div className="font-medium">Returned</div>
                        <div className="text-muted-foreground">
                          {format(new Date(assignment.returned_at), 'MMM d, yyyy')}
                        </div>
                      </div>
                    </div>
                  )}
                </div>

                {assignment.notes && (
                  <div className="pt-2 border-t">
                    <div className="text-sm">
                      <div className="font-medium mb-1">Notes</div>
                      <div className="text-muted-foreground">{assignment.notes}</div>
                    </div>
                  </div>
                )}

                <div className="flex justify-end pt-2">
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => router.push(`/dashboard/equipment/assignments/${assignment.id}`)}
                  >
                    View Details
                  </Button>
                </div>
              </CardContent>
            </Card>
          ))
        )}
      </div>

      {pagination.total_pages > 1 && (
        <div className="flex items-center justify-between">
          <div className="text-sm text-muted-foreground">
            Showing {assignments.length} of {pagination.total} assignments
          </div>
          <div className="flex gap-2">
            <Button
              variant="outline"
              size="sm"
              disabled={pagination.page <= 1}
              onClick={() => {
                if (pagination.page > 1) {
                  setPagination(prev => ({ ...prev, page: prev.page - 1 }))
                  loadAssignments()
                }
              }}
            >
              Previous
            </Button>
            <Button
              variant="outline"
              size="sm"
              disabled={pagination.page >= pagination.total_pages}
              onClick={() => {
                if (pagination.page < pagination.total_pages) {
                  setPagination(prev => ({ ...prev, page: prev.page + 1 }))
                  loadAssignments()
                }
              }}
            >
              Next
            </Button>
          </div>
        </div>
      )}
    </div>
  )
}